import React from "react";
import { motion } from "framer-motion";

const Loader = ({
  size = 40,
  color = "text-blue-500",
  center = false,
  dir = "row",
  className = "",
  textSize = "",
  children,
}) => {
  const dotCommon = `rounded-full ${color} bg-current`;

  return (
    <div
      className={`flex  items-center gap-3 ${
        center ? "justify-center w-full h-full" : ""
      } ${className} ${dir === "row" ? "flex-row" : "flex-col"}`}
    >
      <div className="flex items-center gap-1">
        {[0, 1, 2].map((i) => (
          <motion.span
            key={i}
            className={dotCommon}
            style={{ width: size / 5, height: size / 5 }}
            animate={{
              y: [0, -size / 6, 0],
              opacity: [0.5, 1, 0.5],
            }}
            transition={{
              repeat: Infinity,
              repeatDelay: 0.2,
              duration: 0.8,
              ease: "easeInOut",
              delay: i * 0.2,
            }}
          />
        ))}
      </div>

      {children && (
        <div
          className={`${
            textSize ? textSize : "text-sm"
          } text-muted-foreground text-center`}
        >
          {children}
        </div>
      )}
    </div>
  );
};

export default Loader;
